<template>
  <span style="word-break: break-all">
    <template v-if="readonly">
      {{ inputVal || inputVal === 0 ? inputVal : emptyText }}
    </template>
    <InputNumber
      v-else
      :value="inputVal"
      v-bind="$attrs"
      :disabled="disabled"
      :placeholder="disabled ? '' : placeholder"
      v-on="$listeners"
      @on-change="onChange"
    />
  </span>
</template>

<script>
export default {
  name: 'XInputNumber',
  model: {
    prop: 'value',
    event: 'change',
  },
  props: {
    value: {
      type: Number,
    },
    // 
    placeholder: {
      type: String,
      default: '',
    },
    // 
    readonly: {
      type: Boolean,
      default: false,
    },
    // 
    disabled: {
      type: Boolean,
      default: false,
    },
    // 
    emptyText: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      inputVal: null,
    };
  },
  watch: {
    value: {
      handler(val) {
        this.inputVal = this.$utils.isNull(val) ? null : val;
      },
      immediate: true,
    },
  },
  methods: {
    onChange(value) {
      this.inputVal = this.$utils.isNull(value) ? null : value;
      this.$emit('change', this.inputVal);
    },
  },
};
</script>

<style scoped></style>
