        // component: r => asyncComponent(r(require('@/views/docs/BackTop.md'))),
        component: load('BackTop', 'components'),
        // component: r => require.ensure([], () => r(require('@/views/docs/BackTop.md')), 'components'),
        meta: { title: 'BackTop ' /*, icon: 'md-arrow-up'*/ },
