<template>
  <span style="word-break: break-all">
    <template v-if="readonly">
      {{ selectedName || emptyText }}
    </template>
    <CheckboxGroup
      v-else
      :value="selectedVal"
      v-bind="$attrs"
      v-on="$listeners"
      @on-change="onChange"
    >
      <Checkbox
        v-for="(item, index) in optionsData"
        :key="index"
        :label="item[fields.value]"
        :border="border"
        :disabled="disabled || item.disabled"
      >
        <slot name="prefix" :item="item" :index="index" />
        <template>{{ item[fields.label] }}</template>
        <slot name="append" :item="item" :index="index" />
      </Checkbox>
    </CheckboxGroup>
  </span>
</template>

<script>
export default {
  name: 'XCheckboxGroup',
  model: {
    prop: 'value',
    event: 'change',
  },
  props: {
    value: {
      type: Array,
    },
    // 
    options: {
      type: Array,
      default: () => [],
    },
    // key
    // dictGroupKey: [String, Number],
    //  options 
    replaceFields: {
      type: Object,
      default: () => {
        return { label: 'label', value: 'value' };
      },
    },
    // 
    placeholder: {
      type: String,
      default: '',
    },
    // 
    border: {
      type: Boolean,
      default: false,
    },
    // 
    readonly: {
      type: Boolean,
      default: false,
    },
    // 
    disabled: {
      type: Boolean,
      default: false,
    },
    // 
    emptyText: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      selectedVal: [],
      optionsData: [],
    };
  },
  computed: {
    // 
    fields() {
      const def = { label: 'label', value: 'value' };
      return { ...def, ...this.replaceFields };
    },
    // 
    selected() {
      return this.optionsData.filter(item => this.selectedVal.includes(item[this.fields.value]));
    },
    selectedName() {
      return this.selected.map(item => item[this.fields.label]).join(', ');
    },
    // 
    valueType() {
      const item = this.optionsData[0];
      if (item) {
        return typeof item[this.fields.value];
      }
      return null;
    },
    // 
    propValue() {
      return {
        value: this.value,
        type: this.valueType,
      };
    },
    // 
    // propOptions() {
    //   return {
    //     options: this.options,
    //     dictGroupKey: this.dictGroupKey,
    //   };
    // },
  },
  watch: {
    // value: {
    //   handler(val) {
    //     this.selectedVal = val;
    //   },
    //   immediate: true,
    // },
    propValue: {
      handler(data) {
        if (data.type === 'string' && (data.value || data.value === 0)) {
          this.selectedVal = data.value.map(item => `${item}`);
        } else {
          this.selectedVal = data.value || [];
        }
      },
      immediate: true,
      deep: true,
    },
    options: {
      handler(val) {
        this.optionsData = this.$utils.isNull(val) ? [] : val;
      },
      deep: true,
      immediate: true,
    },
    // propOptions: {
    //   handler(val) {
    //     if (this.dictGroupKey) {
    //       this.$api.dictGroup
    //         .children(this.dictGroupKey)
    //         .then(res => {
    //           const data = res?.data || [];
    //           this.optionsData = data.filter(item => item.state == 1);
    //         })
    //         .catch(() => {});
    //     } else {
    //       this.optionsData = val.options;
    //     }
    //   },
    //   immediate: true,
    //   deep: true,
    // },
  },
  methods: {
    onChange(value) {
      this.selectedVal = this.$utils.isNull(value) ? [] : value;
      this.$emit('change', this.selectedVal);
    },
  },
};
</script>

<style scoped></style>
